
import { defineComponent, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
export default defineComponent({
    name: "warp-footer",
    setup() {
        const router = useRouter();
        const bar = reactive<{ nav: object[] }>({
            nav: [
                { title: "服务协议", type: 1 },
                { title: "隐私政策", type: 2 },
            ],
        });

        const handleRouter = (type: number): void => {
            router.push({
                name: "privacy",
                query: { type },
            });
        };
        return {
            ...toRefs(bar),
            handleRouter,
        };
    },
});
