<div class="article-details"><van-nav-bar title="文萃精编" left-arrow :fixed="true" :safe-area-inset-top="true" @click-left="onClickLeft"></van-nav-bar><div class="details"><van-skeleton title :row="3" :loading="loading"> <div class="article-info mb-40"><div class="title mb-40">{{info.title}}</div><div class="date mb-20">{{info.from_to}}</div><div class="author">{{info.authorization}}</div></div></van-skeleton><van-skeleton :row="3" :loading="loading"> <div class="sentence mb-70"><img class="icon-left mb-15" src="@/assets/images/left-quotes.png"><p class="mb-15">{{info.brief_introduction}}</p><img class="icon-right" src="@/assets/images/right-quotes.png"></div></van-skeleton><van-skeleton title :row="10" :loading="loading"> <div class="rich-content" v-html="info.content"></div></van-skeleton></div><warp-footer></warp-footer><back-top mode="square" duration="smooth" :scrollTop="scrollTop"></back-top></div>